import React from 'react';
import {Link} from "react-router-dom"

function Signup() {
    return ( 
        <div class="app" id="app">
    
      <div class="center-block w-xxl w-auto-xs p-y-md">
        <div class="navbar">
          <div class="pull-center">
            <div ui-include="'../views/blocks/navbar.brand.html'"></div>
          </div>
        </div>

        <div class="p-a-md box-color r box-shadow-z1 text-color m-a">
          <div class="m-b text-sm">Sign up to checkout</div>
          <form name="form">
            <div class="md-form-group">
              <input type="text" class="md-input" required />
              <label>Name</label>
            </div>
            <div class="md-form-group">
              <input type="email" class="md-input" required />
              <label>Email</label>
            </div>
            <div class="md-form-group">
              <input type="password" class="md-input" required />
              <label>Password</label>
            </div>
            <div class="m-b-md">
              <label class="md-check">
                <input type="checkbox" required /><i class="primary"></i> Agree
                the <a href>terms and policy</a>
              </label>
            </div>
            <button type="submit" class="btn primary btn-block p-x-md">
              Sign up
            </button>
          </form>
        </div>

        <div class="p-v-lg text-center">
          <div>
            Already have an account?
            <Link
              ui-sref="access.signin"
              to="signin"
              class="text-primary _600"
              >Sign in</Link>
          </div>
        </div>
      </div>

    </div>
     );
}

export default Signup;