import React from "react";
import "./herobox.css"
import "../../../components/buttons/button.css"

function Herobox() {
  return (
    <div className="hero-main">
      <h1 className="hero-heading">
        Monitor your cattle activity from
        <br />
      </h1>
      <h1 className="hero-heading-2">Anywhere in the World</h1>

      <div className="hero-button">
        <button className="hero-button-1 btn btn-block btn-fw primary">
          Get Started
        </button>
      </div>
    </div>
  );
}

export default Herobox;
