import React from 'react';
import "./team.css"
import talha from "../../images/talha.jpeg"
import sadeed from "../../images/sadeed.jpeg"
import ismail from "../../images/ismail.jpg"
import abubakar from "../../images/abubakar.jpeg"

function Team() {
    return ( 
        <div className="team">
        <h2 className="team-heading">Our Team</h2>
        <div className="team-main">
          <div>
            <img className="team-image" src={talha} alt="" />
            <h4>Talha Ahmad</h4>
            <p></p>
          </div>
          <div>
            <img className="team-image" src={sadeed} alt="" />
            <h4>Sadeed Waseem</h4>
            <p></p>
          </div>
          <div>
            <img className="team-image" src={ismail} alt="" />
            <h4>Sheikh Ismail</h4>
            <p></p>
          </div>
          <div>
            <img className="team-image" src={abubakar} alt="" />
            <h4>Abubakar Nawaz</h4>
            <p></p>
          </div>
        </div>
      </div>
  
     );
}

export default Team;