import { useState } from "react";
import Homepage from "./pages/homepage/Homepage";
import Product from "./pages/product/Product";
import Cart from "./pages/cart/Cart.jsx";
import Team from "./pages/team/Team";
import Contact from "./pages/contact/Contact";
import CardBox from "./pages/homepage/cardbox/CardBox";
import Navbar from "./components/navbar/Navbar";
import Footer from "./components/footer/Footer";
import { Route, Routes } from "react-router-dom";
import ViewDetail from "./pages/viewdetail/ViewDetail";
import Sigin from "./pages/signin/Sigin";
import Signup from "./pages/signup/Signup";
import Forgot from "./pages/forgot/Forgot";

function App() {
  var [detail, setDetail] = useState({});
  var [cart, setCart] = useState([]);

  function appfunc(val) {
    setDetail(val);
  }

  function cartfun(val) {
    setCart((prev) => {
      return [...prev, val];
    });
  }

  console.log(cart);

  return (
    <div className="App">
      <Navbar />
      <Routes>
        <Route exact path="/" element={<Homepage />}></Route>
        <Route
          exact
          path="/product"
          element={<CardBox func={appfunc} cart={cartfun} />}
        ></Route>
        <Route exact path="/cart" element={<Cart />}></Route>
        <Route exact path="/team" element={<Team />}></Route>
        <Route
          exact
          path="/product/detail"
          element={<ViewDetail prod={detail} />}
        ></Route>
        <Route
          exact
          path="/product/cart"
          element={<Cart cartprop={cart} fun={setCart} />}
        ></Route>
        <Route exact path="/contact" element={<Contact />}></Route>

        <Route exact path="/signin" element={<Sigin />}></Route>
        <Route exact path="/signup" element={< Signup />}></Route>
        <Route exact path="/forgot" element={< Forgot />}></Route>
      </Routes>

      <Footer />
      
    </div>
  );
}

export default App;
