import React from "react";
import "./navbar.css";
import "../buttons/button.css";
import { Link } from "react-router-dom";

function Navbar() {
  return (
    <div className="nav-main">
      <nav className="navbar navbar-expand-lg navbar-light">
        <div className="container-fluid">
          <Link className="nav-logo navbar-brand" to="/products">
            BRAND STORE
          </Link>
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarNav"
            aria-controls="navbarNav"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <div
            className="nav-navigation collapse navbar-collapse"
            id="navbarNav"
          >
            <ul className="navbar-nav">
              <li className="nav-navigation-1 nav-item">
                <Link className="nav-link" aria-current="page" to="/">
                Home
                </Link>
              </li>

              <li className="nav-navigation-1 nav-item">
                <Link className="nav-link" aria-current="page" to="/product">
                PRODUCTS
                </Link>
              </li>
              <li className="nav-navigation-4 nav-item">
                <Link className="nav-link" to="/prod/cart">CART</Link>
              </li>

              <li className="nav-navigation-4 nav-item">
                <Link className="nav-link" to="/team">TEAM</Link>
              </li>

              <li className="nav-navigation-4 nav-item">
                <Link className="nav-link" to="/contact">CONTACT US</Link>
              </li>

            </ul>
          </div>

          <div className="nav-button">
            <Link className="nav-button_1 btn btn-fw primary" to="/signin">
              SIGNIN
            </Link>
            <Link
              className="nav-button_2 btn btn-outline b-primary text-primary"
              to="/signup"
            >
             SIGNUP
            </Link>
          </div>
        </div>
      </nav>
    </div>
  );
}

export default Navbar;
