import { click } from "@testing-library/user-event/dist/click";
import React from "react";
import { NavLink } from "react-router-dom";
import {Link} from "react-router-dom";
import ViewDetail from "../../viewdetail/ViewDetail";

import "./card.css"

function Card(props) {
  function clickHandler(event){
    props.func(props);
  }

  function clickHandleradd(event){
    props.cart(props);
  }
  return (
    <div className="card main-card item">
      <img src={props.image} className="card-img-top card-image " alt="..." />
      <div className="card-body card_body">
        <h5 className="card-title">{props.name}</h5>
        <p  className="card-title card-company ">{props.company}</p>
        <h5 className="card-title">{props.price}</h5>
        
        <button onClick={clickHandleradd} className="btn primary spacing_negetive spacing">
        <Link to="cart" >Add to Cart</Link>
        </button>
        
        <button onClick={clickHandler} className="btn primary spacing">
          <Link to="detail" >View Detail</Link>
        </button>
      </div>
    </div>
  );
}

export default Card;
