import React from "react";
import './cart.css'

const Cart = (props) => {

  const handleRemove = (id) => {
    const arr = props.cartprop.filter((item) => item.id !== id);
    props.fun(arr);
    
  };
 
  return (
    <div>
      {props.cartprop.map((item)=>{
        return(
          <div className="cart_box article" key={item.id}>
          <div className="cart_img">
            <img src={item.image} alt="" />
            <p style={{marginTop:"15px",marginLeft:"60px"}}>{item.name}</p>
            <p style={{marginTop:"15px",marginLeft:"60px"}}>{item.company}</p>
            <p style={{marginTop:"15px",marginLeft:"60px"}}>{item.category}</p>
          </div>
       
          <div>
            <span style={{marginRight:"60px"}}>{item.price}</span>
            <button className="btn primary " onClick={() => handleRemove(item.id)}>Remove</button>
         
          </div>
        </div>
        );
      })}
     
</div>
  );
};

export default Cart;
