import React from "react";
import "./working.css";

function Working() {
  return (
    <div class="work-main">
      <h2 class="work-heading">HOW IT WORKS &mdash; SIMPLE AS 1, 2, 3</h2>
      <div className="work-steps">
        <span className="work-steps-no">1</span>
        <p className="work-steps-text">Get your account setup</p>
      </div>
      <div className="work-steps">
        <span className="work-steps-no">2</span>
        <p className="work-steps-text">
          Attach the prototype to cattle collar and leg.
        </p>
      </div>
      <div className="work-steps">
        <span className="work-steps-no">3</span>
        <p className="work-steps-text">
          Connect the product with the internet.
        </p>
      </div>

      <h6 style={{ marginLeft: "30%", marginTop: " 35px" }}>
        And you are ready to go....
      </h6>
    </div>
  );
}

export default Working;
