import React from "react";
import {Link} from "react-router-dom"
import './signin.css'

function Sigin() {
  return (
    <div className="app" id="app">
      <div className="center-block w-xxl w-auto-xs p-y-md">
        <div className="p-a-md box-color r box-shadow-z1 text-color m-a">
          <div className="m-b text-sm">
            Sign in with your Administrative Account
          </div>
          <form name="form">
            <div className="md-form-group float-label">
              <input
                type="email"
                className="md-input"
                ng-model="user.email"
                required
              />
              <label>Email</label>
            </div>
            <div className="md-form-group float-label">
              <input
                type="password"
                className="md-input"
                ng-model="user.password"
                required
              />
              <label>Password</label>
            </div>
            <div className="m-b-md">
              <label className="md-check">
                <input type="checkbox" />
                <i className="primary"></i> Keep me signed in
              </label>
            </div>
            <button type="submit" className="btn primary btn-block p-x-md">
              Sign in
            </button>
          </form>
        </div>

        <div className="p-y-lg text-center">
          <div className="m-b">
            <Link
              ui-sref="access.forgot-password"
              to="/forgot"
              className="text-primary _600"
            >
              Forgot password?
            </Link>
          </div>
          <div>
            Do not have an account?
            <Link
              ui-sref="access.signup"
              to="signup"
              className="text-primary _600"
            >
              Sign up
            </Link>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Sigin;
