import React from "react";
import "./newArrival.css";
import "../buttons/button.css"

import shoe from "../../images/black.png";
import logo from "../../images/logo.png";

function NewArrival() {
  return (
    <div className="new_arrival">
      <div className="arrival_container ">
        <div className="arrival_card">
          <div className="arrival_shoeBackground">
            <div className="arrival_gradients">
              <div className="arrival_gradient" color="arrival_black"></div>
            </div>
            <h1 className="arrival_nike">nike</h1>
            <img src={logo} alt="" className="arrival_logo" />
            <img src={shoe} alt="" className="arrival_shoe" color="black" />
          </div>

          <div className="arrival_info">
            <div className="arrival_shoeName">
              <div>
                <h1 className="arrival_big">Nike Zoom KD 12</h1>
                <span className="arrival_new">new</span>
              </div>
              <h3 className="arrival_small">Men's running shoes</h3>
            </div>
            <div className="arrival_description">
              <h3 className="arrival_title">Product Info</h3>
              <p className="arrival_text">
                Lorem Ipsum is simply dummy text of the printing and typesetting
                industry. Lorem Ipsum has been the industry's.
              </p>
            </div>
            
            <div className="arrival_size-container">
              <h3 className="arrival_title">size</h3>
              <div className="arrival_sizes">
                <span className="arrival_size">7</span>
                <span className="arrival_size">8</span>
                <span className="arrival_size active">9</span>
                <span className="arrival_size">10</span>
                <span className="arrival_size">11</span>
              </div>
            </div>
            <div className="arrival_buy-price">
              <a href="#" className="buy nav-button_1 btn btn-fw primary">
                <i className="arrival_fas arrival_fa-shopping-cart "></i>Add to
                card
              </a>
              <div className="arrival_price">
                <i className="arrival_fas arrival_fa-dollar-sign"></i>
                <h1>189.99</h1>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default NewArrival;
