import React from "react";
import {Link} from "react-router-dom"

function Forgot() {
  return (
    <div className="app" id="app">
      <div className="center-block w-xxl w-auto-xs p-y-md">
        <div className="navbar">
          <div className="pull-center">
            <div ui-include="'../views/blocks/navbar.brand.html'"></div>
          </div>
        </div>
        <div className="p-a-md box-color r box-shadow-z1 text-color m-a">
          <div className="m-b">
            Forgot your password?
            <p className="text-xs m-t">
              Enter your email address below and we will send you instructions
              on how to change your password.
            </p>
          </div>
          <form name="reset">
            <div className="md-form-group">
              <input type="email" className="md-input" required />
              <label>Your Email</label>
            </div>
            <button type="submit" className="btn primary btn-block p-x-md">
              Send
            </button>
          </form>
        </div>
        <p id="alerts-container"></p>
        <div className="p-v-lg text-center">
          Return to
          <Link
            ui-sref="access.signin"
            to="signin"
            className="text-primary _600"
          >
            Sign in
          </Link>
        </div>
      </div>
    </div>
  );
}

export default Forgot;
