import React from "react";
import "./contact.css";

function Contact() {
  return (
    <div className="bg-white">
      <div id="app" className="app contact_us_form">
        <div className="p-a-md r text-color bg-white">
          <div className="contact_us_padding">
            <h3 className="contact_h2">CONTACT US</h3>
            <div className="md-form-group contact-form">
              <input type="text" className="md-input" required />
              <label>Your Name</label>
            </div>

            <div className="md-form-group contact-form">
              <input type="email" className="md-input" required />
              <label>Email</label>
            </div>

            <div className="md-form-group contact-form">
              <textarea className="md-input form_text_area" rows="1"></textarea>
              <label>Message</label>
            </div>
            <div className="d-md-block">
              <button
                type="submit"
                className="btn primary p-x-md contact_us_btn"
                style={{ width: "250px" }}
              >
                Submit
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Contact;
