import React from 'react';
import "./viewdetail.css";

function ViewDetail(props) {
    return ( 
        <div className="product-center" key={props.prod.id}>
            <img className="prod-photo" src={props.prod.image} alt={props.prod.name} />
            <section className="content-prod">
              <h2>{props.prod.name}</h2>
              
            
              <h5 className="price-prod">{props.prod.price}</h5>
              <p className="description"> {props.prod.description}</p>
              <p className="info-prod">
                <span>Available : </span>
                In stock
              </p>

              {/* {{ stock } && <AddToCart product={products} />} */}
            </section>
          </div>
     );
}

export default ViewDetail;